import React, { useEffect, useState } from 'react';
import { NavLink } from 'react-router-dom';
 
function    FeatureProduct() {
    let [filtered, setfiltered] = useState([]);
    let fData = [];
    useEffect(() => {
        const fetchdata = async () => {
            try {
                const res = await fetch('http://localhost:3003');
                const data = await res.json();
                let i = 0;
                for (i = 0; i < data.length; i++) {
                    if (data[i].featured === "true") {
                        fData = [...fData, data[i]];
                    }
                }
                
                setfiltered(fData);
                

            } catch (error) {
                console.log(error);
            };
        };
        fetchdata();
    }, []);
    return (
        <div className='container'>
            <p>CHECK NOW!</p>
            <h1>Our Featured Services</h1>
            <div className='row'>
                 {filtered.map(element => {
                                return (
                                    <NavLink className="col" to={`/singleProduct/${element._id}`} key={element._id} style={{ textDecoration:"none",color:"black"}} >
                                        <div class="card">
                                          
                                            <img src={element.image} class="card-img-top" alt="image" />
                                            <div class="card-body ">
                                                <span class="fs-5 ">{element.name}</span>
                                                <span class="fs-5 " style={{marginLeft:"12rem",color:"blue"}}>${element.price}
                                                    
                                                </span>
                                            </div>
                                           
                                        </div>
                                    </NavLink>)
                            })
                        }
            </div>
        </div>
    );
}

export default FeatureProduct;